footer{
    background-color: var(--color-primary);
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;
}

footer a{
    color: var(--color-bg);
}

.footerLogo{
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;
}

.permalinks{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    margin: 0 auto 1rem;
}

.footerSocials{
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 2rem;
    font-size: 1.5rem;
}

.footerSocials a{
    background-color: var(--color-bg);
    color: white;
    padding: 0.8rem;
    border-radius: 0.7rem;
    display: flex;
    border: 1px solid transparent;
    transition: var(--transition);
}

.footerSocials a:hover{
    background-color: transparent;
    color: var(--color-bg);
    border-color: var(--color-bg);
}

.footerCR{
    margin-bottom: 1rem;
    color: var(--color-bg);
}

@media screen and (max-width:600px) {
    
    .permalinks{
        flex-direction: column;
        gap: 1rem;
        margin: 0 auto 2rem;
    }

}